  * {
   box-sizing: border-box;
    margin: 0 auto;
  }
  
  .container {
    min-width: 375px;
    max-width: 1350px;
    margin: 0 auto;
    padding:0 15px;
  }
  
  .container-big {
    min-width: 375px;
    max-width: 1440px;
    margin: 0 auto;
  }
  
  .body {
    font-family: "Montserrat", "Arial", sans-serif;
    font-style: normal;
  }
  
  .big-wrap {
    background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
    border-radius: 0px 0px 12px 12px;
    position: relative;
    
  }
  
  .big-wrap::after {
    content: '';
    position: absolute;
    border-radius: 0px 0px 12px 12px;
    width: calc(100% - 40px);
    height: 10px;
    left: 20px;
    bottom: -10px;
    background-color: #CACADB;
  }
  
  .big-wrap::before {
    content: '';
    position: absolute;
    border-radius: 0px 0px 12px 12px;
    width: calc(100% - 80px);
    height: 10px;
    left: 40px;
    bottom: -20px;
    background-color: #E7E7F2;
  }
  
  .small-wrap {
    background-image: url(../img/small-h.svg);
    background-repeat: no-repeat;
  }
  
  .header__wrap {
    display: grid;
    grid-template-columns: 1fr min-content;
    grid-template-rows: auto auto;
    grid-template-areas: "top top"
                         "logobox menu";
  }
  
  .header__topbox {
    grid-area: top;
    display: flex;
    justify-content: space-between;
    background: rgba(245, 245, 250, 0.2);
    padding: 15px;
  }
  
  .header__logo {
     grid-area: logobox;
     background-image: url(im);
     padding: 15px 0 15px 15px;
  }
  
  .header__menu {
    grid-area: menu;
  }
  
